import CustomersList from './CustomersList';
import React, { useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { Grid } from '@material-ui/core';
import AutoCompleteField, { SelectOption } from 'components/form/AutoCompleteField';
import Card from 'components/Card';
import PaddedContainer from 'components/PaddedContainer';
import { productTourResumeDelay, useProductTour } from 'features/ProductTour';
import usePrevious from 'hooks/usePrevious';
import UploadField from 'components/form/UploadField';
// import { useRenderCount } from 'hooks/useRenderCount';

interface CustomersForm {
  HasInsurance: SelectOption<string> | undefined;
  InsuranceProvider: SelectOption<string> | undefined;
  PolicyDocument: (File | string)[] | undefined;
}

const defaultValues: CustomersForm = {
  HasInsurance: undefined,
  InsuranceProvider: undefined,
  PolicyDocument: undefined,
}

const insuranceOptions: SelectOption<string>[] = [
  { label: "Yes", value: "true" },
  { label: "No", value: "false" },
]

const insuranceProviders: SelectOption<string>[] = [
  { label: "QBE", value: "1" },
  { label: "Trade Credit", value: "2" },
  { label: "Other", value: "10" },
]

function Customers() {
  const [{ run }, tourActions] = useProductTour();
  // const count = useRenderCount();
  // const insuranceCardRef = React.useRef<HTMLDivElement | null>(null);
  const methods = useForm<CustomersForm>({
    defaultValues,
    mode: 'onChange',
  });

  const {
    handleSubmit,
    watch,
    control,
  } = methods;

  const hasInsuranceField = watch('HasInsurance');
  const hasInsuranceValue = hasInsuranceField?.value;
  const hasInsurance = hasInsuranceField?.value === 'true';
  const prevHasInsuranceValue = usePrevious(hasInsuranceValue);

  // Insurance field has changed during tour, move to final step
  useEffect(
    () => {
      if (run && !prevHasInsuranceValue && !!hasInsuranceValue) {
        tourActions.pause();
        tourActions.setStep(4);
        setTimeout(() => {
          tourActions.resume();
        }, productTourResumeDelay);
      }
    },
    [run, hasInsuranceValue, prevHasInsuranceValue, tourActions]
  );

  // console.log('Customers Renders:', count);

  return (
    <PaddedContainer>
      <form onSubmit={handleSubmit(() => {})}>
        <Grid container spacing={2}>
          <Grid item xs={12}>
            <Card title="Customers to fund">
              <CustomersList />
            </Card>
          </Grid>
          <Grid item xs={12}>
            <Card
              title="Insurance"
              ariaLabel="InsuranceDetails"
              // ref={insuranceCardRef}
            >
              <Grid container spacing={2}>
                <Grid item xs={12} md={6}>
                  <AutoCompleteField
                    name="HasInsurance"
                    control={control}
                    label="Do you have BDP Insurance?"
                    options={insuranceOptions}
                  />
                </Grid>
                <Grid item xs={12} md={6}>
                  {
                    hasInsurance &&
                    <AutoCompleteField
                      name="InsuranceProvider"
                      control={control}
                      label="Insurance provider"
                      options={insuranceProviders}
                    />
                  }
                </Grid>
                {
                  hasInsurance &&
                  <Grid item xs={12} md={6}>
                    <UploadField
                      name="PolicyDocument"
                      label="Policy Document"
                      control={control}
                    />
                  </Grid>
                }
              </Grid>
            </Card>
          </Grid>
        </Grid>
      </form>
    </PaddedContainer>
  )
}

export default Customers;
