/** @jsx jsx */
import { css, jsx } from '@emotion/core';
import MUIDataTable, { MUIDataTableColumn } from "mui-datatables";
import { MdFileUpload } from "react-icons/md";
import React from 'react';
import { Button, Switch } from '@material-ui/core';
import AbnDialog from 'components/AbnDialog';
import RightAlignCell from 'components/table/RightAlignCell';
import options from 'components/table/options';
import { formatMoney } from 'utils/number';

const UploadIconClass = css`
  margin-right: 3px;
`;

const data = [
  ["Customer Pty Ltd", "987 654 3210", undefined, 203124.12, true],
  ["XYZ Pty Ltd", "", undefined, 45145, true],
  ["Blankety Pty Ltd", "", undefined, 12074.00, false],
];

function CustomersList() {
  const [isAbnModalOpen, setIsAbnModalOpen] = React.useState(false);

  const columns: MUIDataTableColumn[] = [
    {
      name: "Name"
    },
    {
      name: "", // ABN
      options: {
        customBodyRender: (value, tableMeta, updateValue) => {
          const isFunding = tableMeta.rowData[tableMeta.rowData.length-1]
          return (
            !value && isFunding ?
            <Button
              variant="outlined"
              size="small"
              onClick={() => setIsAbnModalOpen(!isAbnModalOpen)}
            >
              Add ABN
            </Button>
            : value
          )
        }
      }
    },
    {
      name: "", // Contract
      options: {
        customBodyRender: (value, tableMeta, updateValue) => {
          const isFunding = tableMeta.rowData[tableMeta.rowData.length-1]
          return (
            !value && isFunding ?
            <Button
              variant="outlined"
              size="small"
            >
              <MdFileUpload css={UploadIconClass} /> Contract
            </Button>
            : null
          )
        }
      }
    },
    {
      name: "Balance",
      options: {
        setCellHeaderProps: (columnMeta) => ({
          style: {
            textAlign: 'right'
          }
        }),
        customBodyRender: (value, tableMeta, updateValue) => 
          <RightAlignCell>{formatMoney(Number(value))}</RightAlignCell>
      }
    },
    {
      name: "", // Fund
      options: {
        customBodyRender: (value: any, tableMeta, updateValue) => {
          return (
            <Switch
              checked={value}
              color="primary"
              inputProps={{ 'aria-label': 'primary checkbox' }}
            />
          )
        }
      }
    }
  ];

  function onAbnModalClose() {
    setIsAbnModalOpen(false);
  }

  function onAbnModalSubmit() {
    setIsAbnModalOpen(false);
  }

  return (
    <React.Fragment>
      <MUIDataTable
        title=""
        data={data}
        columns={columns}
        options={options}
      />
      <AbnDialog
        isOpen={isAbnModalOpen}
        onClose={onAbnModalClose}
        onSubmit={onAbnModalSubmit}
      />
    </React.Fragment>
  )
}

export default CustomersList;
