/** @jsx jsx */
import { css, jsx } from '@emotion/core';
import { Button, Dialog, DialogActions, DialogContent, DialogTitle } from '@material-ui/core';
import TextField from 'components/form/TextField';
import { useForm } from 'react-hook-form';

const ContentStyle = css`
  min-width: 350px;
`

interface SearchForm {
  FindAbn: string;
}

const defaultValues: SearchForm = {
  FindAbn: '',
}

export interface AbnDialogProps {
  isOpen: boolean;
  onClose: () => void;
  onSubmit: (abn: string) => void;
}

function AbnDialog({ isOpen, onClose, onSubmit }: AbnDialogProps) {
  const methods = useForm<SearchForm>({
    defaultValues,
    mode: 'onChange',
  });

  const {
    handleSubmit,
    control,
    getValues,
  } = methods;

  function onAbnModalClose() {
    onClose();
  }

  function onAbnModalOK() {
    onSubmit(getValues('FindAbn'));
  }

  return (
    <Dialog
      open={isOpen}
      onClose={onAbnModalClose}
      aria-labelledby="alert-dialog-title"
      aria-describedby="alert-dialog-description"
    >
      <DialogTitle id="alert-dialog-title">
        Find ABN
      </DialogTitle>
      <DialogContent css={ContentStyle}>
        <form onSubmit={handleSubmit(() => {})}>
          <TextField
            name="FindAbn"
            control={control}
            autoFocus
            label="Provide name or ABN"
          />
        </form>
      </DialogContent>
      <DialogActions>
        <Button onClick={onAbnModalClose}>
          Cancel
        </Button>
        <Button variant="contained" onClick={onAbnModalOK}>
          OK
        </Button>
      </DialogActions>
    </Dialog>
  )
}

export default AbnDialog;