import Decimal from "decimal.js-light";

const injectThousandsSeparator = (value: string) =>
  value.replace(/\B(?=(\d{3})+(?!\d))/g, ',');

export const formatMoney = (value: number, formatThousands = true, decimals = true): string => {
  const formatted = new Decimal(value).toFixed(decimals ? 2 : undefined);
  return formatThousands ? injectThousandsSeparator(formatted) : formatted;
};

export const formatInteger = (value: number): string =>
  injectThousandsSeparator(value.toString());
